export const CAFE = {
  TIME: {
    OPEN_KOR: "",
    CLOSE_KOR: "",
    OPEN_TIME: "10:00",
    CLOSE_TIME: "22:00",
  },
};

export const DAYS = [
  "",
  "",
  "",
  "",
  "",
  "",
  "",
];

export function getDefaultOpenHourList() {
  const openHourList = [];
  for (let i = 0; i < DAYS.length; i++) {
    openHourList.push({
      day: DAYS[i],
      time: `${CAFE.TIME.OPEN_TIME} ~ ${CAFE.TIME.CLOSE_TIME}`,
    });
  }
  return openHourList;
}
